import * as React from 'react'
import { styled } from '@mui/material/styles'
import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell, { tableCellClasses } from '@mui/material/TableCell'
import TableContainer from '@mui/material/TableContainer'
import Collapse from '@mui/material/Collapse'
import IconButton from '@mui/material/IconButton'
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown'
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp'
import Typography from '@mui/material/Typography'
import CircularProgress from '@mui/material/CircularProgress'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import Paper from '@mui/material/Paper'
import Box from '@mui/system/Box'

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.primary.main,
    color: theme.palette.common.black,
    fontSize: 18,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 16,
  },
}))

const StyledNestedTableCell = styled(TableCell)(({ theme }) => ({
  padding: 0
}))

const StyledNestedTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-child(4n + 1) ': {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}))

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}))

const StyledNoResult = styled(StyledTableCell)(({ theme }) => ({
  textAlign: 'center',
  width: '100%',
  padding: '100px 0px',
}))

const LoadingContainer = styled(Box)(({ theme }) => ({
  width: '100%',
  height: '100%',
  position: 'absolute',
  top: 0,
  left: 0,
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  zIndex: 9,
}))

const renderHeaderCell = ({ id, title, align = 'left' }) => {
  if (id === '_collapse') {
    return <StyledTableCell align={align} />
  }

  return <StyledTableCell align={align}>{title}</StyledTableCell>
}

const renderCell = ({ id, align = 'right', renderComponent, style = {} }, item, index, open, setOpen) => {
  if (id === '_collapse') {
    return (
      <StyledTableCell align={align} style={style}>
        <IconButton aria-label="expand row" size="small" onClick={() => setOpen(!open)}>
          {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
        </IconButton>
      </StyledTableCell>
    )
  }

  if (renderComponent) {
    return (
      <StyledTableCell align={align} style={style}>
        {renderComponent(item, index)}
      </StyledTableCell>
    )
  }

  return <StyledTableCell align={align}>{item[id]}</StyledTableCell>
}

const ExpandableRow = ({ data, columns, index }) => {
  const [open, setOpen] = React.useState(false)

  return (
    <>
      <StyledNestedTableRow key={data.id}>
        {columns.map((column) => renderCell(column, data, index, open, setOpen))}
      </StyledNestedTableRow>
      <TableRow key={data.id}>
        <StyledNestedTableCell colSpan={columns.length}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <CustomizedTables data={[]}/>
          </Collapse>
        </StyledNestedTableCell>
      </TableRow>
    </>
  )
}

export default function CustomizedTables({ isNestedTable = false, columns = [], data = [], loading = false,handleListItemClick = ()=>{} ,...props }) {
  return (
    <TableContainer component={Paper} {...props} >
      {/* {loading && (
        <LoadingContainer>
          <CircularProgress size={30} />
        </LoadingContainer>
      )} */}
      <Table sx={{ minWidth: 700}} stickyHeader aria-label="customized table">
        <TableHead>
          <TableRow>{columns.map((column) => renderHeaderCell(column))}</TableRow>
        </TableHead>
        <TableBody >
          {loading ? (
            <StyledTableRow>
              <StyledNoResult colSpan={columns.length}>
                <CircularProgress size={30} />
              </StyledNoResult>
            </StyledTableRow>
          ) : data.length === 0 ? (
            <StyledTableRow>
              <StyledNoResult colSpan={columns.length}>
                <Typography>No result found.</Typography>
              </StyledNoResult>
            </StyledTableRow>
          ) : (
            data.map((row, index) => {
              if (isNestedTable) {
                return <ExpandableRow data={row} columns={columns} index={index} />
              }
              return (
                <StyledTableRow key={row.id} onClick = { () => handleListItemClick(row)}>{columns.map((column) => renderCell(column, row, index))}</StyledTableRow>
              )
            })
          )}
        </TableBody>
      </Table>
    </TableContainer>
  )
}
