/* eslint-disable no-else-return */
import { useCallback, useEffect, useState } from 'react'
import { useWeb3React, UnsupportedChainIdError } from '@web3-react/core'
import { setupNetwork } from 'utils/wallet'
import keccak256 from 'keccak256'

import {
  InjectedConnector,
  NoEthereumProviderError,
  UserRejectedRequestError as UserRejectedRequestErrorInjected,
} from '@web3-react/injected-connector'
import {
  UserRejectedRequestError as UserRejectedRequestErrorWalletConnect,
  WalletConnectConnector,
} from '@web3-react/walletconnect-connector'
import { connectorLocalStorageKey, connectorsByName, signedStorageKey } from 'utils/web3React'
import { addDay, getMillisecondsTimestamp } from 'utils/dateTime'
import useWeb3 from './useWeb3'
import useSignMessage from './useSignMessage'

const useAuth = () => {
  const [isAuth, setIsAuth] = useState(false)
  const { activate, deactivate, account } = useWeb3React()
  const web3 = useWeb3()

  const { onHandelSigMessage } = useSignMessage()
  const logout = useCallback(() => {
    deactivate()
    // This localStorage key is set by @web3-react/walletconnect-connector
    if (window.localStorage.getItem('walletconnect')) {
      connectorsByName.walletconnect.close()
      connectorsByName.walletconnect.walletConnectProvider = null
    }
    window.localStorage.removeItem(connectorLocalStorageKey)
  }, [deactivate])

  const login = useCallback((connectorID) => {
    const connector = connectorsByName[connectorID]

    if (connector) {
      activate(connector, async (error) => {
        if (error instanceof UnsupportedChainIdError) {
          if (connector instanceof InjectedConnector && window.ethereum.isMetaMask) {
            const hasSetup = await setupNetwork()
            if (hasSetup) {
              activate(connector)
            }
          }
          if (window.localStorage.getItem('walletconnect')) {
            window.localStorage.removeItem('walletconnect')
            window.localStorage.removeItem(connectorLocalStorageKey)
          }
        } else {
          window.localStorage.removeItem(connectorLocalStorageKey)
          if (error instanceof NoEthereumProviderError) {
          } else if (
            error instanceof UserRejectedRequestErrorInjected ||
            error instanceof UserRejectedRequestErrorWalletConnect
          ) {
            if (connector instanceof WalletConnectConnector) {
              const walletConnector = connector
              walletConnector.walletConnectProvider = null
            }
          }
        }
      })
    }
  }, [])

  const signData = useCallback(
    async (address) => {
      if (account) {
        const n_account = account.toLowerCase()
        const expireTime = getMillisecondsTimestamp(addDay(1))
        const message = `Authorization: ${n_account}|${expireTime}`
        const { signedData, error } = await onHandelSigMessage(message)
        if (signedData) {
          setIsAuth(true)
          sessionStorage.setItem(
            signedStorageKey,
            JSON.stringify({
              address:n_account,
              signedData,
              expireTime,
            }),
          )
        } else {
          console.log(error.message, 'errorsignedData')
        }
      }

      return null
    },
    [web3, account],
  )
  useEffect(() => {
    if (account) {
      const signed = JSON.parse(sessionStorage.getItem(signedStorageKey))
      if (signed) {
        if (signed.address.toLowerCase() !== account.toLowerCase()) {
          setIsAuth(false)
          sessionStorage.removeItem(signedStorageKey)
        } else {
          setIsAuth(true)
        }
      }
    }
  }, [account])

  return { login, logout, signData, isAuth }
}

export default useAuth
