/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit'

const initialState = {
  modal: {
    isShow: false,
    modalName: null,
    data: {},
  },
  depositInfo: {},
  timezone: null,
  depositsRewardQuantity: null
}

export const appSlice = createSlice({
  name: 'App',
  initialState,
  reducers: {
    setModal: (state, action) => {
      const { modalName, data } = action.payload
      state.modal.modalName = modalName
      state.modal.data = data
      state.modal.isShow = true
    },
    setTimeZone: (state, action) => {
      state.timezone = action.payload
    },
    setDepositInfo: (state, action) => {
      state.depositInfo = action.payload
    },
    setDepositsRewardQuantity: (state, action) => {
      state.depositsRewardQuantity = action.payload
    },
    clearModal: (state, action) => {
      state.modal.isShow = false
    },
  },
})

// Actions
export const { setModal, setTimeZone, setDepositInfo, clearModal,setDepositsRewardQuantity } = appSlice.actions
// Selectors
export const selectModalStatus = (state) => state.app.modal.isShow
export const selectModalName = (state) => state.app.modal.modalName
export const selectModalData = (state) => state.app.modal.data
export const selectDepositInfo = (state) => state.app.depositInfo
export const selectDepositsRewardQuantity = (state) => state.app.depositsRewardQuantity
export const selectTimezone = (state) => state.app.timezone

export default appSlice.reducer
