import React from 'react'
import Box from '@mui/material/Box'
import TextField from '@mui/material/TextField'
import { styled } from '@mui/material/styles'

const StyledTextField = styled(TextField)({})

const LabelInput = ({
  label,
  variant = 'outlined',
  type = 'text',
  focused = false,
  sxContainer = {},
  sxInput = {},
  value = '',
  autoComplete='off',
  onChange = () => {},
  ...props
}) => (
  <Box
    sx={{
      width: '100%',
      ...sxContainer,
    }}
  >
    <StyledTextField
      sx={sxInput}
      type={type}
      label={label}
      variant={variant}
      focused={focused}
      value={value}
      onChange={(e) => onChange(e)}
      autoComplete="off"
      {...props}
    />
  </Box>
)

export default LabelInput
