import React, { useEffect } from 'react'
import { HashRouter, Switch, Route, Redirect } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import BigNumber from 'bignumber.js'
import { QueryParamProvider } from 'use-query-params'
import useEagerConnect from 'hooks/useEagerConnect'
import AuthRoute from 'utils/AuthRoute'
import { setTimeZone } from 'state/reducers/app'
import NavBar from './layout/Navbar'
import RewardManagement from './pages/RewardManagement'
import UserManagement from './pages/UserManagement'
import Login from './pages/Login'

// Config for big number
BigNumber.set({
  EXPONENTIAL_AT: 100,
})

function App() {
  const dispatch = useDispatch()
  useEagerConnect()

  useEffect(() => {
    const timezone = Intl.DateTimeFormat().resolvedOptions().timeZone
    dispatch(setTimeZone(timezone))
  }, [])

  return (
    <HashRouter basename="/">
      <QueryParamProvider {...{ path: '/' }} ReactRouterRoute={Route}>
        <NavBar>
          <Switch>
            <Route exact path="/" render={() => <Redirect to="/reward-management" />} />
            <Route path="/reward-management">
              <AuthRoute component={RewardManagement} />
            </Route>
            <Route path="/login">
              <Route component={Login} />
            </Route>
          </Switch>
        </NavBar>
      </QueryParamProvider>
    </HashRouter>
  )
}

export default App
