import React from 'react'
import Box from '@mui/material/Box'
import TextField from '@mui/material/TextField'
import AdapterDateFns from '@mui/lab/AdapterDateFns'
import LocalizationProvider from '@mui/lab/LocalizationProvider'
import DatePicker from '@mui/lab/DatePicker'

export default function BasicDateTimePicker({
  value,
  label,
  onChange = () => {},
  sxContainer = {},
  hourConfig = { h: 0, m: 0, s: 0, ms: 0 },
  ...props
}) {
  const { h, m, s, ms } = hourConfig

  const handleChange = (newVal) => {
    try {
      if (!newVal) {
        onChange(null)
        return
      }
      let newValueInTimestamp = 0
      newValueInTimestamp = newVal.setHours(h, m, s, ms)
      onChange(newValueInTimestamp)
    } catch (error) {
      console.log(error, 'fail to change time')
    }
  }

  return (
    <Box sx={sxContainer}>
      <LocalizationProvider dateAdapter={AdapterDateFns}>
        <DatePicker
          renderInput={(prs) => <TextField {...prs} />}
          label={label}
          value={value}
          onChange={handleChange}
          {...props}
        />
      </LocalizationProvider>
    </Box>
  )
}
