import React, { useState, useMemo } from 'react'
import { useLocation, useHistory } from 'react-router-dom'
import { useTheme } from '@mui/material/styles'
import Box from '@mui/material/Box'
import Drawer from '@mui/material/Drawer'
import CssBaseline from '@mui/material/CssBaseline'
import List from '@mui/material/List'
import Divider from '@mui/material/Divider'
import IconButton from '@mui/material/IconButton'
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft'
import ChevronRightIcon from '@mui/icons-material/ChevronRight'
import ListItem from '@mui/material/ListItem'
import Typography from '@mui/material/Typography'
import ListItemText from '@mui/material/ListItemText'
import { urlConfig } from 'constant'
import { drawerWidth } from 'constant/styles'
import CyballLogo from 'assets/images/cyball-logo.png'
import { DrawerHeader, Main, LogoImage } from './styles'
import TopBar from '../Topbar'

export default function NavBar({ children }) {
  const theme = useTheme()
  const { pathname } = useLocation()
  const history = useHistory()
  const isOpen = localStorage.getItem('navBarStatus')
  const [open, setOpen] = useState(false)

  const handleDrawerOpen = () => {
    localStorage.setItem('navBarStatus', true)
    setOpen(true)
  }

  const handleDrawerClose = () => {
    localStorage.setItem('navBarStatus', false)
    setOpen(false)
  }

  const onNavigateTo = (route) => {
    if (route) {
      history.push(route)
    }
  }

  const URL_CONFIG = useMemo(() => urlConfig, [])

  return (
    <Box sx={{ display: 'flex' }}>
      <CssBaseline />
      <TopBar open={open} handleDrawerOpen={handleDrawerOpen} />
      <Drawer
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            width: drawerWidth,
            boxSizing: 'border-box',
          },
        }}
        variant="persistent"
        anchor="left"
        open={open}
      >
        <DrawerHeader>
          <LogoImage onClick={() => {
            history.push('/')
          }}>
            <img src={CyballLogo} alt="Cyball Logo" width="100px" />
          </LogoImage>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
          </IconButton>
        </DrawerHeader>
        <Divider />
        <List>
          {URL_CONFIG.map(({ name, path, Icon }) => {
            const isActive = path === '/' ? pathname === path : pathname.indexOf(path) === 0
            return (
              <ListItem
                button
                key={name}
                selected={isActive}
                onClick={() => {
                  onNavigateTo(path)
                }}
              >
                <Icon
                  sx={{
                    fontSize: 25,
                    mr: 2,
                  }}
                />
                <ListItemText>
                  <Typography variant="body1">{name}</Typography>
                </ListItemText>
              </ListItem>
            )
          })}
        </List>
      </Drawer>
      <Main open={open}>
        <DrawerHeader />
        {children}
      </Main>
    </Box>
  )
}
