import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import Typography from '@mui/material/Typography'
import Box from '@mui/material/Box'
import { styled } from '@mui/material/styles'
import ButtonComponent from 'components/Button'
import useAlert from 'components/Alert/useAlert'
import LabelInput from 'components/LabelInput'
import { clearModal, selectModalData } from 'state/reducers/app'
import { getRewardDetail } from 'service/api'
import { useAppDispatch } from 'state'
import { formatCurrency } from 'utils/formatCurrency'
import get from 'lodash/get'

const Container = styled(Box)(({ theme }) => ({
  marginBottom: theme.spacing(3),
  display: 'flex',
  width: '100%',
}))
const ItemList = ({ title, value }) => {
  return (<Box sx={{ width: '100%', display: 'flex', justifyContent: "space-between" }}>
      <Typography color='text.secondary' sx={{marginRight: '20px'}}>{`${title}:`}</Typography>
      <Typography>{value}</Typography>
  </Box>)

}

const ModalRewardDetail = () => {
  const { id } = useSelector(selectModalData)
  const [isLoading, setIsLoading] = useState(false)
  const dispatch = useAppDispatch()
  const [error, setError] = useState(null)
  const [data, setData] = useState(null)

  const fetchRewardDetail = async (id) => {
    try {
      setIsLoading(true)
      let res = await getRewardDetail(id)
      setData(res.data && res.data.extraInfo)
      setIsLoading(false)
    } catch (error) {
      console.log(error, 'Reward detail error?')
      setError_msg(error.message)
      setIsLoading(false)
    }
  }

  useEffect(() => {
    fetchRewardDetail(id)
  }, [id])

  return (
    <Container>
      {isLoading && <div>Loading...</div>}
      {error ? <div>Error: {error.toString()}</div> : <></>}
      {data && (
        <Box sx={{ width: '100%' }}>
          <ItemList title="Win" value={data.winMatch} />
          <ItemList title="Total Match" value={data.totalMatch} />
          <ItemList title="Own CyBloc" value={data.ownCyBloc} />
          <ItemList title="Leased CyBloc" value={data.leasedCyBloc} />
          <ItemList title="Listed CyBloc" value={data.listedCyBloc} />
          <ItemList title="Borrowing CyBloc" value={data.borrowingCyBloc} />
          <ItemList title="Recall CyBloc" value={data.recallCyBloc || data.recallCybloc} />
          <ItemList title="Reward from Borrower" value={`${formatCurrency(data.rewardFromBorrower)} CBT`} />
          <ItemList title="Reward from Borrow CyBloc" value={`${formatCurrency(data.rewardFromBorrowCyBloc)} CBT`} />
          <ItemList title="Reward from Own CyBloc" value={`${formatCurrency(data.rewardFromOwnCyBloc)} CBT`} />
        </Box>
      )}
      
      {error && <Typography color="red">{error.message}</Typography>}
    </Container>
  )
}

export default ModalRewardDetail
