import React, { useMemo, useEffect, useState } from 'react'
import { useWeb3React } from '@web3-react/core'
import { useQueryParams, StringParam, withDefault, NumberParam } from 'use-query-params'
import Typography from '@mui/material/Typography'
import Box from '@mui/material/Box'
import Tooltip from '@mui/material/Tooltip'
import get from 'lodash/get'
import Table from 'components/Table'
import Button from 'components/Button'
import StatusBar from 'components/StatusBar'
import useAlert from 'components/Alert/useAlert'
import { getRewardData, getRewardSummary , rewardApprovement } from 'service/api'
import { formatDateFromTimestamp, formatTimeFromTimestamp } from 'utils/dateTime'
import ALERT_TYPE from 'constant/alert'
import useAuth from 'hooks/useAuth'
import { formatAddress } from 'utils/formatAddress'
import { formatCurrency } from 'utils/formatCurrency'
import REWARD_STATUS_CONSTANT from 'constant/rewardStatus'
import configs from 'config'
import { useAppDispatch } from 'state'
import MODAL from 'constant/modal'
import { setModal } from 'state/reducers/app'
import AdvancedFilters from './components/AdvancedFilters'
import DepositInfo from './components/DepositInfo'
import Action from './Action'

const RewardManagement = () => {
  const { isAuth } = useAuth()
  const { account } = useWeb3React()
  const dispatch = useAppDispatch()
  const [refetching, setRefetching] = useState(0)
  const [isApproveAll, setIsApproveAll] = useState(false)
  const [continuationToken, setContinuationToken] = useState(null)
  const [hasMoreResults, setHasMoreResults] = useState(false)
  const [isLoadingTableData, setIsLoadingTableData] = useState(true)
  const [isLoadingMore, setIsLoadingMore] = useState(false)
  const [data, setData] = useState([])
  const [rewardSummaryData, setRewardSummaryData] = useState([])
  const [onPresentAlert] = useAlert()
  const [query, setQuery] = useQueryParams({
    rewardStatus: withDefault(StringParam, 'all'),
    address: withDefault(StringParam, ''),
    startTime: withDefault(NumberParam, 0),
    endTime: withDefault(NumberParam, new Date().setHours(23, 59, 59, 0)),
    rewardId: withDefault(StringParam, ''),
  })
  const { rewardStatus, startTime, endTime, address } = query
  const refetchingData = () => {
    setRefetching(refetching + 1)
  }

  const onUpdateRewardStatus = (newData) => {
    setData(newData)
  }

    // Handel Reward Detail Action

    const onHandelShowRewardDetail = (rewardId) =>{

      dispatch(
        setModal({
          modalName: MODAL.MODAL_REWARD_DETAIL,
          data: {
            id:rewardId,   
          },
        }),
      )
    }
  

  const columns = useMemo(
    () => [
      {
        id: 'STT',
        title: 'STT',
        align: 'center',
        renderComponent: (item, index) => <Typography>{`${index + 1}`}</Typography>,
      },
      {
        id: 'id',
        title: 'Id/Address',
        align: 'left',
        renderComponent: (item) => (
          <Box>
            <Typography
              sx={{
                color: 'gray',
                cursor: 'pointer',
                textDecoration: 'underline'
              }}
              onClick = {()=>{onHandelShowRewardDetail(item.id)}}
            >{`${item.id}`}</Typography>
            <Typography sx={{ marginTop: '2px' }}>
              <b>{`${item.address}`}</b>
            </Typography>
          </Box>
        ),
      },
      {
        id: 'endTime',
        title: 'Claim Time',
        align: 'left',
        renderComponent: (item) => (
          <Box>
            {item.endTime === 0 ? (
              <> </>
            ) : (
              <>
                <Typography
                  sx={{
                    fontSize: '12px',
                  }}
                >
                  {formatTimeFromTimestamp(Math.floor(item.endTime / 1000))}
                </Typography>
                <Typography
                  sx={{
                    fontSize: '16px',
                  }}
                >
                  {formatDateFromTimestamp(Math.floor(item.endTime / 1000))}
                </Typography>
              </>
            )}
          </Box>
        ),
      },
      {
        id: 'amount',
        title: 'Amount',
        align: 'right',
        numeric: true,
        disablePadding: false,
        // renderComponent: (item) => <Typography>{formatCurrency(get(item, 'amount'))}</Typography>,
        renderComponent: (item) => (
          <Box>
            <Tooltip
              title={
                <Typography>
                  {formatCurrency(get(item, 'amount'), 4)}
                </Typography>
              }
            >
              <Box>
                {formatCurrency(Math.floor(get(item, 'amount')))}
              </Box>
            </Tooltip>
          </Box>
        ),
      },
      // {
      //   id: 'claimRewardId',
      //   title: 'Claim Reward Id',
      //   style: {
      //     maxWidth: '100px',
      //   },
      //   align: 'right',
      // },
      {
        id: 'status',
        title: 'Status',
        align: 'center',
        renderComponent: (item) => (
          <Box>
            <Tooltip
              title={
                <Typography>
                  {item && item.claimRewardId ? `claimId: ${item.claimRewardId}` : 'Have not sign data'}
                </Typography>
              }
            >
              <Box>
                <StatusBar status={item.status} rejectMessage={item.message} />
              </Box>
            </Tooltip>
          </Box>
        ),
      },
      {
        id: 'actions',
        title: 'Actions',
        align: 'center',
        style: {
          minWidth: '200px',
        },
        renderComponent: (item) => (
          <Action
            item={item}
            refetchData={refetchingData}
            query={query}
            onUpdateRewardStatus={onUpdateRewardStatus}
            rewardList={data}
          />
        ),
      },
    ],
    [query, data],
  )

  // Fetch reward summary
  useEffect(() => {
    const fetchSummary = async () => {
      try {
        const rewardSumRes = await getRewardSummary(startTime, endTime, '', rewardStatus)
        const summaryData = get(rewardSumRes, 'data', {})
        setRewardSummaryData(summaryData)
      } catch (error) {
        onPresentAlert(ALERT_TYPE.ERROR, 'Fail to loading reward summary')
        // setIsLoadingTableData(false)
      }
    }

    if (account) {
      fetchSummary()
    }
  }, [account, refetching, query])

  const fetchRewardList = async (sTime, eTime, address, rStatus, cToken) => {
    try {
      if (!cToken) {
        setIsLoadingTableData(true)
      } else {
        setIsLoadingMore(true)
      }
      const result = await getRewardData(sTime, eTime, address, rStatus, cToken)
      const itemList = get(result, 'data.items', [])
      const responseHasMoreResult = get(result, 'data.hasMoreResults', false)
      const responseContinuationToken = get(result, 'data.continuationToken', null)
      setHasMoreResults(responseHasMoreResult)
      setContinuationToken(responseContinuationToken)
      // We appending new item when there is new continuation token
      if (cToken) {
        setData([...data, ...itemList])
      } else {
        setData(itemList)
      }
      setIsLoadingTableData(false)
      setIsLoadingMore(false)
    } catch (error) {
      onPresentAlert(ALERT_TYPE.ERROR, 'Fail to loading reward list')
      setIsLoadingTableData(false)
      setIsLoadingMore(false)
    }
  }

  // Fetch reward list
  useEffect(() => {
    if (account) {
      fetchRewardList(startTime, endTime, address, rewardStatus, continuationToken)
    }
  }, [account, refetching])

  // This use effect is used to refetch reward list when user change query param
  useEffect(() => {
    if (account) {
      fetchRewardList(startTime, endTime, address, rewardStatus, null)
    }
  }, [startTime, endTime, address, rewardStatus])

  // Handel APPROVE ALL
  const onHandleApproveAll = async () => {
    var confirmed = confirm("Please confirm this action!")
    if (!confirmed) return
    try {
      setIsApproveAll(true)
      let newRewardData = [...data]
      // chainId, contratRewardAddress, claimId, rewardId, account, amount, pKey
      for (let index = 0; index < data.length; index += 1) {
        const status = get(data, `${index}.status`, '')
        if (status !== REWARD_STATUS_CONSTANT.INPROCESS) {
          continue
        }
        const id = get(data, `${index}.id`, null)
        if (id) {
          await rewardApprovement(id, true, '')
          newRewardData = newRewardData.map((el) => {
            if (el.id === id) {
              return {
                ...el,
                status: REWARD_STATUS_CONSTANT.APPROVED,
              }
            }
            return el
          })
          onUpdateRewardStatus(newRewardData)
        } else {
          throw new Error('Cant get id reward')
        }
      }

      onPresentAlert(ALERT_TYPE.SUCCESS, 'Successfully Approve All')
      setIsApproveAll(false)
    } catch (error) {
      console.error(error)
      setIsApproveAll(false)
      onPresentAlert(ALERT_TYPE.ERROR, 'Fail Approve All')
    }
  }

  const onEnterSignerPrivateKey = () => {

  }


  return (
    <>
      {isAuth ? (
        <>
          <Box sx={{ display: ' flex', flexDirection: 'row' }}>
            <Box sx={{ flex: 1, padding: '10px', borderRadius: '10px', backgroundColor: 'background.secondary' }}>
              <AdvancedFilters query={query} setQuery={setQuery} />
            </Box>
            <Box
              sx={{
                flex: 1,
                ml: '20px',
                padding: '10px',
                borderRadius: '10px',
                backgroundColor: 'background.secondary',
              }}
            >
              <DepositInfo
                data={rewardSummaryData}
                query={query}
                setQuery={setQuery}
                refetchingData={refetchingData}
                rewardData={data}
                onUpdateRewardStatus={onUpdateRewardStatus}
              />
            </Box>
          </Box>
          <Box sx={{ mt: '20px', display: 'flex', justifyContent: 'end' }}>
            <Button
              style={{
                marginRight: '10px',
              }}
              size="small"
              color="success"
              disabled={
                isLoadingTableData ||
                (rewardStatus !== REWARD_STATUS_CONSTANT.INPROCESS && rewardStatus !== REWARD_STATUS_CONSTANT.ALL) ||
                data.length === 0
              }
              isLoading={isApproveAll}
              onClick={() => {
                onHandleApproveAll()
              }}
            >
              Approve All Rewards List
            </Button>
            <Button
              size="small"
              disabled={isLoadingTableData}
              onClick={() => {
                if (isLoadingTableData) {
                  return
                }
                setContinuationToken(null)
                refetchingData()
              }}
            >
              Refesh Rewards List
            </Button>
          </Box>
          <Table sx={{ mt: '20px' }} columns={columns} data={data} loading={isLoadingTableData} />
          <Box
            mt={(theme) => theme.spacing(1.5)}
            sx={{
              textAlign: 'center',
            }}
          >
            <Button
              disabled={!hasMoreResults}
              isLoading={isLoadingMore}
              onClick={() => {
                refetchingData()
              }}
            >
              LOAD MORE
            </Button>
          </Box>
        </>
      ) : (
        <></>
      )}
    </>
  )
}

export default RewardManagement
