import Http from 'utils/HttpUtils'

export const getRewardData = (startTime, endTime, address, status, continuationToken) =>
  Http.get('/rewards', {
    params: {
      endTime,
      startTime,
      address,
      status,
      continuationToken
    },
  })

export const rewardApprovement = (id, approved, message) =>
  Http.post('/reward-approvement', {
    id,
    approved,
    message,
  })

export const getRewardSummary = (startTime, endTime, address, status) =>
  Http.get('/reward-review-summary', {
    params: {
      status,
      endTime,
      startTime,
      address,
    },
  })
  export const getRewardSessions = (number, limit) =>
  Http.get('/RewardSessions?from=number', {
    params: {
      number,
      limit
    },
  })

export const rewardSign = (id, claimRewardId, proof) => {
  return Http.post('/reward-sign-request', {
    id,
    claimRewardId,
    proof,
  })
}
export const getRewardDetail = (id) => 
  Http.get('/RewardDetails', {
    params: {
      id,
    },
  })


