import BigNumber from 'bignumber.js'
import { getBalanceAmount } from './formatBalance'

export const generateRewardId = (sTime, eTime) =>
  new BigNumber(sTime)
    .multipliedBy(10 ** 16)
    .plus(eTime)
    .toString()

export const newGenerateRewardId = (curTime,quantity) =>
  new BigNumber(curTime)
    .multipliedBy(10**9)
    .plus(quantity+1)
    .toString()

export const formatDepositInfo = (depositInfo) => {
  const { max, signer, total, token, rewardId } = depositInfo
  const result = {
    rewardId,
    signer,
    token,
    maxAmount: getBalanceAmount(max).toString(),
    total: getBalanceAmount(total).toString(),
  }

  return result
}
