import BigNumber from 'bignumber.js'
import { BIG_TEN } from 'constant/bignumber'

BigNumber.set({
  EXPONENTIAL_AT: 25,
})

export const getDecimalAmount = (amount, decimals = 18) => new BigNumber(amount).times(BIG_TEN.pow(decimals))

export const getBalanceAmount = (amount, decimals = 18) => new BigNumber(amount).dividedBy(BIG_TEN.pow(decimals))

export const getBalanceNumber = (balance, decimals = 18) => getBalanceAmount(balance, decimals).toNumber()

export const getFullDisplayBalance = (balance, decimals = 18, decimalsToAppear = 2) => getBalanceAmount(balance, decimals).toFixed(decimalsToAppear)

export const getAmountString = (amount, decimals = 3) => amount.toFixed(decimals)
