import MuiAccordionDetails from '@mui/material/AccordionDetails'
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography'
import ButtonComponent from 'components/Button'
import { useAppDispatch } from 'state'
import { selectDepositInfo, setDepositInfo, setModal } from 'state/reducers/app'
import useIsApproved from 'hooks/useIsApprove'
import { useApprove } from 'hooks/useApprove'
import { useCyballRewardContract } from 'hooks/useContract'
import useWeb3 from 'hooks/useWeb3'
import { getBep20Contract } from 'utils/contractHelpers'
import REWARD_STATUS_CONSTANT from 'constant/rewardStatus'
import MODAL from 'constant/modal'
import { ZERO_ADDRESS } from 'constant/web3'
import configs from 'config'


const ActionButton = ({ isLoading, isApproved, onApprove, dispatch, query, data, onRefetchingDepositInfo = () => { } }) => {
    const { startTime, endTime } = query

    if (isLoading) {
        return <ButtonComponent isLoading />
    }
    if (isApproved) {
        return (
            <ButtonComponent
                color='success'
                onClick={() => {
                    dispatch(
                        setModal({
                            modalName: MODAL.MODAL_DEPOSIT,
                            data: {
                                data,
                            },
                        }),
                    )
                }}
            >
                Deposit
            </ButtonComponent>
        )
    }

    return (
        <ButtonComponent onClick={onApprove} isLoading={isLoading}>
            Approve token
        </ButtonComponent>
    )
}

const DepositAction = ({ data, query }) => {

    const [isLoading, setIsLoading] = useState(false)

    const dispatch = useAppDispatch()
    const web3 = useWeb3()
    const { rewardStatus } = query
    const tokenContract = useMemo(() => getBep20Contract(configs.CBT_TOKEN_REWARD, web3), [web3])
    const [isApproved, reloadCheckApproval, isLoadingApproved] = useIsApproved(
        tokenContract,
        configs.REWARD_CONTRACT_ADRESS,
    )
    const { onApprove } = useApprove(tokenContract, configs.REWARD_CONTRACT_ADRESS)




    const onHandleApproveToken = async () => {
        setIsLoading(true)
        const res = onApprove()
        await reloadCheckApproval()
        setIsLoading(false)
    }




    return (
        <div>

            <Box sx={{display:'flex', marginLeft: '10px', justifyContent:'center'}}>
                <ActionButton
                    data={data}
                    dispatch={dispatch}
                    isLoading={isLoadingApproved || isLoading}
                    isApproved={isApproved}
                    query={query}
                    onApprove={onHandleApproveToken}
                />
            </Box>

            {/* {(rewardStatus === REWARD_STATUS_CONSTANT.APPROVED) && (

            )} */}



        </div>
    )
}

export default DepositAction
