import React, { useState, useMemo } from 'react'
import { fromRpcSig } from 'ethereumjs-util'
import useAlert from 'components/Alert/useAlert'
import ButtonComponent from 'components/Button'
import { rewardApprovement, rewardSign } from 'service/api'
import ALERT_TYPE from 'constant/alert'
import REWARD_STATUS_CONSTANT from 'constant/rewardStatus'
import { setModal, selectDepositInfo } from 'state/reducers/app'
import { useAppDispatch } from 'state'
import MODAL from 'constant/modal'
import { useWeb3React } from '@web3-react/core'
import { useSelector } from 'react-redux'
import configs from 'config'
import { generateRewardId } from 'utils/reward'
import Web3 from 'web3'
import useWeb3 from 'hooks/useWeb3'
import get from 'lodash'
import { getDecimalAmount } from 'utils/formatBalance'
import { ZERO_ADDRESS } from 'constant/web3'
import { FamilyRestroomRounded } from '@mui/icons-material'

import { generateProofClaim } from 'utils/web3'

const Action = ({ item, refetchData = () => { }, query, onUpdateRewardStatus, rewardList }) => {
  const [onPresentAlert] = useAlert()
  const web3 = useWeb3()
  const { account, chainId } = useWeb3React()
  const dispatch = useAppDispatch()
  const { signer , rewardId} = useSelector(selectDepositInfo)
  const [isApproving, setIsApproving] = useState(false)
  const [isSigning, setIsSigning] = useState(false)
  const { startTime, endTime } = query
  const { status, id } = item

  const isDisabled = useMemo(() => {
    if (status === REWARD_STATUS_CONSTANT.INPROCESS) {
      return false
    }

    return true
  }, [status])

  const onHandleApprove = async () => {
    try {
      setIsApproving(true)
    await rewardApprovement(id, true, '')
      onPresentAlert(ALERT_TYPE.SUCCESS, `Successfully approve reward ${id}`)
      let newRewardData = [...rewardList]
      newRewardData = newRewardData.map((el) => {
        if (el.id === id) {
          return {
            ...el,
            status: REWARD_STATUS_CONSTANT.APPROVED,
          }
        }
        return el
      })
     
      onUpdateRewardStatus(newRewardData)
      setIsApproving(false)
    } catch (error) {
      onPresentAlert(ALERT_TYPE.ERROR, 'Fail to approve reward')
      setIsApproving(false)
    }
  }

  const onHandleSignDataToClaim = async() => {

    if (item && account && signer.toLowerCase() === account.toLowerCase()) {
      // if signer is account metamask => open metamask sign massage
      try {
        setIsSigning(true)
        const decimalAmount = getDecimalAmount(item.amount, configs.CBT_TOKEN_DECIMALS)
        const msg = Web3.utils.soliditySha3(chainId, configs.REWARD_CONTRACT_ADRESS, rewardId, id, item.address, decimalAmount)
        const sig = await web3.eth.sign(msg, account)
        const proof = fromRpcSig(sig)
        const ret = {v: proof.v, r:proof.r.toJSON().data,  s:proof.s.toJSON().data}
 
        await rewardSign(id, rewardId, JSON.stringify(ret))
        let newRewardData = [...rewardList]
        newRewardData = newRewardData.map((el) => {
          if (el.id === id) {
            return {
              ...el,
              status: REWARD_STATUS_CONSTANT.READYCLAIMED,
            }
          }
          return el
        })
       
        onUpdateRewardStatus(newRewardData)
        onPresentAlert(ALERT_TYPE.SUCCESS, 'Successfully sign data')
        setIsSigning(false)
      } catch (error) {
        console.log(error, ' error')
        onPresentAlert(ALERT_TYPE.ERROR, error.message)
        setIsSigning(false)
 
      }

    } else {
      var pk = window.prompt("Enter Signer's private key", "")
      const decimalAmount = getDecimalAmount(item.amount, configs.CBT_TOKEN_DECIMALS)
      const proof = await generateProofClaim(
        chainId,
        configs.REWARD_CONTRACT_ADRESS,
        rewardId,
        id,
        item.address,
        decimalAmount.toString(),
        pk,
      )
      await rewardSign(id, rewardId, JSON.stringify(proof))
      let newRewardData = [...rewardList]
        newRewardData = newRewardData.map((el) => {
          if (el.id === id) {
            return {
              ...el,
              status: REWARD_STATUS_CONSTANT.READYCLAIMED,
            }
          }
          return el
        })
       
        onUpdateRewardStatus(newRewardData)
        onPresentAlert(ALERT_TYPE.SUCCESS, 'Successfully sign data')

      // dispatch(
      //   setModal({
      //     modalName: MODAL.MODAL_SIGNDATA,
      //     data: {
      //       startTime,
      //       endTime,
      //       rewardData: item,
      //       rewardList,
      //       cb:(()=>{})
      //     },
      //   }),
      // )
    }
  }

  if (status === REWARD_STATUS_CONSTANT.APPROVED) {
    return (
      <ButtonComponent
        style={{
          marginRight: '10px',
        }}
        size="small"
        color="success"
        title = 'signData'
        disabled = {signer === ZERO_ADDRESS}
        isLoading={isSigning}
        onClick={onHandleSignDataToClaim}
      >
        Sign Data
      </ButtonComponent>
    )
  }
  
  if (status === REWARD_STATUS_CONSTANT.INPROCESS){
    return (
      <>
        <ButtonComponent
          style={{
            marginRight: '10px',
          }}
          size="small"
          color="success"
          disabled={isDisabled }
          isLoading={isApproving}
          onClick={() => {
            onHandleApprove()
          }}
        >
          Approve
        </ButtonComponent>
        <ButtonComponent
          size="small"
          color="error"
          disabled={isDisabled || isApproving}
          // isLoading={isRejecting}
          onClick={() => {
            dispatch(
              setModal({
                modalName: MODAL.MODAL_REJECT_REWARD,
                data: {
                  id,
                  rewardList,
                  onUpdateRewardStatus,
                },
              }),
            )
          }}
        >
          Reject
        </ButtonComponent>
      </>
    )
  }
  return (<></>)


}

export default Action
