import React from 'react'
import { useWeb3React } from '@web3-react/core'
import Box from '@mui/material/Box'
import MenuIcon from '@mui/icons-material/Menu'
import IconButton from '@mui/material/IconButton'
import Chip from '@mui/material/Chip'
import Typography from '@mui/material/Typography'
import Button from 'components/Button'
import { useAppDispatch } from 'state'
import { setModal } from 'state/reducers/app'
import MODAL from 'constant/modal'
import { AppBar, HeaderBar } from './styles'

const TopBar = ({ open, handleDrawerOpen }) => {
  const { account } = useWeb3React()
  const dispatch = useAppDispatch()

  const onOpenLoginModal = () => {
    dispatch(setModal({
      modalName: MODAL.MODAL_LOGIN,
      data: {}
    }))
  }

  const onOpenLogoutModal = () => {
    dispatch(setModal({
      modalName: MODAL.MODAL_LOGOUT,
      data: {}
    }))
  }

  return (
    <AppBar position="fixed" open={open}>
      <HeaderBar>
        <Box
          sx={{
            display: 'flex',
          }}
        >
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            sx={{ mr: 2, ...(open && { display: 'none' }) }}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h2" noWrap component="div">
            Cyball Admin Panel
          </Typography>
        </Box>
        {account ? (
            <Chip
              onClick={onOpenLogoutModal}
              label={account}
              variant="outlined"
              sx={{
                typography: 'body1',
                color: '#f0f0f0',
              }}
            />
        ) : (
          <Button variant="contained" size="sm" onClick={onOpenLoginModal}>
            Connect Wallet
          </Button>
        )}
      </HeaderBar>
    </AppBar>
  )
}

export default TopBar
