import http from 'axios'
import configs from '../config'
import { signedStorageKey, connectorLocalStorageKey } from './web3React'

const Http = http.create({
  baseURL: configs.API_URL,
})

// Custom headers
export const addHttpHeaders = (headers) => {
  Http.defaults.headers = {
    ...Http.defaults.headers,
    ...headers,
  }
}

export const getSignedData = () => {
  const data = JSON.parse(sessionStorage.getItem(signedStorageKey))

  if (data) {
    return data
  }

  return null
}

// Request Interceptors
Http.interceptors.request.use((config) => {
  let signedDataStorage = null

  signedDataStorage = getSignedData()

  if (signedDataStorage !== null) {
    const { expireTime, signedData, address } = signedDataStorage
    config.headers['x-auth'] = `${address}-${expireTime}-${signedData}`
    config.headers['x-app'] = 'admin'
  }

  return config
})

Http.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response) {
      const { status, data } = error.response
      // Remove the sign key from local storage if the auth key expired
      if (status === 403 && data && data.error === 'The auth expired') {
        sessionStorage.removeItem(signedStorageKey)
        localStorage.removeItem(connectorLocalStorageKey)
      }

      throw new Error(data && data.error)
    }
  },
)

export default Http
