import { useSelector } from 'react-redux'
import { styled } from '@mui/material/styles'
import Dialog from '@mui/material/Dialog'
import DialogTitle from '@mui/material/DialogTitle'
import DialogContent from '@mui/material/DialogContent'
import Typography from '@mui/material/Typography'
import IconButton from '@mui/material/IconButton'
import CloseIcon from '@mui/icons-material/Close'
import { useAppDispatch } from 'state'
import { clearModal, selectModalName, selectModalStatus } from 'state/reducers/app'
import MODAL from 'constant/modal'
import ModalLogin from './ModalLogin'
import ModalLogout from './ModalLogout'
import ModalApprove from './ModalRejectReward'
import ModalDeposit from './ModalDeposit'
import ModalSignData from './ModalSignData'
import ModalRewardDetail from './ModalRewardDetail'

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
  '& .MuiDialogContent-root': {
    padding: theme.spacing(2),
  },
  '& .MuiDialogActions-root': {
    padding: theme.spacing(1),
  },
}))

const BootstrapDialogTitle = (props) => {
  const { children, onClose, ...other } = props

  return (
    <DialogTitle sx={{ m: 0, p: 2, textAlign: 'center', fontSize: '2.5rem' }} {...other}>
      {children}
      {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </DialogTitle>
  )
}

const CustomizedDialogs = () => {
  const modalName = useSelector(selectModalName)
  const isShowModal = useSelector(selectModalStatus)
  const dispatch = useAppDispatch()

  const handleClose = () => {
    dispatch(clearModal())
  }

  const renderModalContent = () => {
    if (modalName === MODAL.MODAL_LOGIN) {
      return <ModalLogin />
    }

    if (modalName === MODAL.MODAL_LOGOUT) {
      return <ModalLogout />
    }

    if (modalName === MODAL.MODAL_REJECT_REWARD) {
      return <ModalApprove />
    }

    if (modalName === MODAL.MODAL_DEPOSIT) {
      return <ModalDeposit />
    }

    if (modalName === MODAL.MODAL_SIGNDATA) {
      return <ModalSignData />
    }
    if (modalName === MODAL.MODAL_SIGNDATA) {
      return <ModalSignData />
    }
    if (modalName === MODAL.MODAL_REWARD_DETAIL) {
      return <ModalRewardDetail />
    }

    return null
  }

  const renderModalHeader = () => {
    if (modalName === MODAL.MODAL_LOGIN) {
      return 'Connect to a wallet'
    }

    if (modalName === MODAL.MODAL_LOGOUT) {
      return 'Log out'
    }

    if (modalName === MODAL.MODAL_REJECT_REWARD) {
      return 'Reject reward'
    }

    if (modalName === MODAL.MODAL_DEPOSIT) {
      return 'Confirmation'
    }

    if (modalName === MODAL.MODAL_SIGNDATA) {
      return 'Sign Data'
    }
    if (modalName === MODAL.MODAL_REWARD_DETAIL) {
      return 'Reward Detail'
    }


    return null
  }

  return (
    <div>
      <BootstrapDialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={isShowModal}>
        <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose}>
          <Typography variant="h2">{renderModalHeader()}</Typography>
        </BootstrapDialogTitle>
        <DialogContent>{renderModalContent()}</DialogContent>
      </BootstrapDialog>
    </div>
  )
}

export default CustomizedDialogs
