import utcToZonedTime from 'date-fns-tz/utcToZonedTime'
import addDays from 'date-fns/addDays'
import compareAsc from 'date-fns/compareAsc'
import format from 'date-fns/format'
import fromUnixTime from 'date-fns/fromUnixTime'
import getTime from 'date-fns/getTime'
import parseISO from 'date-fns/parseISO'

export const addDay = (numberOfDays) => addDays(new Date(), numberOfDays)

export const getMillisecondsTimestamp = (date) => getTime(date)

export const getCurrentDateTime = (timestamp) => fromUnixTime(timestamp)

export const convertTimeStampToDate = (timestamp) => {
  if (timestamp) {
    const dateIsoFormatted = parseISO(fromUnixTime(timestamp).toISOString())
    const dateWithOffset = new Date(
      dateIsoFormatted.getUTCFullYear(),
      dateIsoFormatted.getUTCMonth(),
      dateIsoFormatted.getUTCDate(),
      dateIsoFormatted.getUTCHours(),
      dateIsoFormatted.getUTCMinutes(),
    )

    return dateWithOffset
  }
  return null
}

export const getUtcDateString = (timestamp) => {
  if (timestamp) {
    const dateWithOffset = convertTimeStampToDate(timestamp)
    return `${format(dateWithOffset, 'MMM dd yyyy HH:mm')} UTC`
  }

  return null
}

export const getDateTimeBaseOnTimeZone = (timestamp) => {
  const timezone = Intl.DateTimeFormat().resolvedOptions().timeZone
  const date = fromUnixTime(timestamp).toISOString()
  if (date && timezone) {
    const time = utcToZonedTime(date, timezone) 
    return format(time, 'MMM dd yyyy HH:mm:a')
  }

  return null
}

// if result equals -1 -> date1 < date2
export const compareDate = (date1, date2) => {
  const result = compareAsc(date1, date2)

  if (result === -1) {
    return true
  }

  return false
}

export const formatDateFromTimestamp = (timestamp) => {
  return new Date(timestamp * 1000).toLocaleDateString('en-US')
}

export const formatTimeFromTimestamp = (timestamp) => {
  return new Date(timestamp * 1000).toLocaleTimeString('en-US')
}

