import React, { useState, useMemo } from 'react'
import { useAppDispatch } from 'state'
import { useSelector } from 'react-redux'
import { useWeb3React } from '@web3-react/core'
import Typography from '@mui/material/Typography'
import Box from '@mui/material/Box'
import get from 'lodash/get'
import ButtonComponent from 'components/Button'
import useAlert from 'components/Alert/useAlert'
import LabelInput from 'components/LabelInput'
import { clearModal, selectModalData, selectDepositInfo } from 'state/reducers/app'
import { rewardSign } from 'service/api'
import ALERT_TYPE from 'constant/alert'
import { generateProofClaim } from 'utils/web3'
import { getDecimalAmount } from 'utils/formatBalance'
import { generateRewardId } from 'utils/reward'
import useWeb3 from 'hooks/useWeb3'
import configs from 'config'
import REWARD_STATUS_CONSTANT from 'constant/rewardStatus'

const ModalSignData = () => {
  const [onPresentAlert] = useAlert()
  const web3 = useWeb3()
  const { chainId } = useWeb3React()
  const [isLoading, setIsLoading] = useState(false)
  const [privateKey, setPrivateKey] = useState('')
  const [publicAddress, setPublicAddress] = useState('')
  const { rewardData, startTime, endTime, onUpdateRewardStatus, rewardList, cb } = useSelector(selectModalData)
  const {signer} = useSelector(selectDepositInfo)
  const dispatch = useAppDispatch()
  const rewardId = useMemo(() => generateRewardId(startTime, endTime), [startTime, endTime])

  const onGetPublicKey = () => {
    try {
      if (web3) {
        const { address } = web3.eth.accounts.privateKeyToAccount(privateKey)
        setPublicAddress(address)
      }
    } catch (error) {
      onPresentAlert(ALERT_TYPE.ERROR, error.message)
    }
  }

  const onHandleSignData = async () => {
    try {
      if (signer && signer.toLowerCase() !== publicAddress.toLowerCase()) {
        onPresentAlert(ALERT_TYPE.ERROR, 'Signing account must be the same with deposit signer')
        return
      }

      setIsLoading(true)
      let newRewardData = [...rewardList]
      // chainId, contratRewardAddress, claimId, rewardId, account, amount, pKey
      const id = get(rewardData, 'id', null)
      const amount = get(rewardData, 'amount', null)
      const address = get(rewardData, 'address', null)
      const decimalAmount = getDecimalAmount(amount, configs.CBT_TOKEN_DECIMALS)
      if (id && amount) {
        const proof = await generateProofClaim(
          chainId,
          configs.REWARD_CONTRACT_ADRESS,
          rewardId,
          id,
          address,
          decimalAmount.toString(),
          privateKey,
        )
        await rewardSign(id, rewardId, JSON.stringify(proof))
      
        newRewardData = newRewardData.map((item) => {
          if (item.id === id) {
            return {
              ...item,
              status:REWARD_STATUS_CONSTANT.READYCLAIMED,
            }
          }

          return item
        })
        cb(newRewardData)
        // onUpdateRewardStatus(newRewardData)
      } else {
        throw new Error('Cant get id or amount')
      }

      onPresentAlert(ALERT_TYPE.SUCCESS, 'Successfully sign data')
      setIsLoading(false)
      dispatch(clearModal())
      // refetchingData()
    } catch (error) {
      setIsLoading(false)
      console.log(error, "ERROR ?")
      onPresentAlert(ALERT_TYPE.ERROR, 'Fail to sign data')
    }
  }

  return (
    <>
      <Box
        mb={(theme) => theme.spacing(1)}
        mt={(theme) => theme.spacing(1)}
        sx={{
          display: 'flex',
        }}
      >
        {/* <LabelInput
          label="Private key"
          type="password"
          autoComplete='off'
          value={privateKey}
          onChange={(e) => setPrivateKey(e.target.value)}
          sxInput={{
            width: '300px',
          }}
        /> */}
        <input type="password" autoComplete='off' style={{outline: 'none', width: '100%'}}
          placeholder="Signer's private key"
          value={privateKey} 
          onChange={(e) => setPrivateKey(e.target.value)} />
        <ButtonComponent disabled={!privateKey} onClick={onGetPublicKey}>
          Check
        </ButtonComponent>
      </Box>
      {publicAddress && (
        <Typography variant="caption" color="green" mb={(theme) => theme.spacing(1)}>
          Public Address: {publicAddress}
        </Typography>
      )}
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          mt: 2,
        }}
      >
        <ButtonComponent
          color="success"
          disabled={!publicAddress}
          isLoading={isLoading}
          onClick={onHandleSignData}
          style={{
            marginRight: '15px',
          }}
        >
          Sign data
        </ButtonComponent>
        <ButtonComponent
          color="error"
          disabled={isLoading}
          onClick={() => {
            dispatch(clearModal())
          }}
        >
          Cancel
        </ButtonComponent>
      </Box>
    </>
  )
}

export default ModalSignData
