import React from 'react'
import Button from '@mui/material/Button'
import CircularProgress from '@mui/material/CircularProgress'
import Typography from '@mui/material/Typography'
import { styled } from '@mui/material/styles'


const StyledButton = styled(Button)(({ theme, color }) => ({
  fontSize: '1.4rem',
  '&:disabled': {
    color: '#f0f0f0',
  },
  ...(color === 'success' &&
    {
      color: 'green',
      border: '1px solid green'
    }),
    ...(color === 'warning' &&
    {
      color: 'red',
      border: '1px solid red'
    }),
}))

const StyledCircularProgress = styled(CircularProgress)(() => ({
  marginRight: '10px',
}))

const ButtonComponent = ({ children, variant = 'outlined', size = 'large', isLoading, disabled, color, ...props }) => (
    <Button variant={variant} size={size} disabled={isLoading || disabled} color={color} {...props}>
      {isLoading && <StyledCircularProgress color="secondary" size="20px" />}
      <Typography margin = 'auto' variant="button">{children}</Typography>
    
    </Button>
  )

export default ButtonComponent
