import React from 'react'
import Typography from '@mui/material/Typography'
import Box from '@mui/material/Box'
import { styled } from '@mui/material/styles'
import { connectorLocalStorageKey, ConnectorNames } from 'utils/web3React'
import useAuth from 'hooks/useAuth'
import { useAppDispatch } from 'state'
import { clearModal } from 'state/reducers/app'

const LoginWallet = styled(Box)(({ theme }) => ({
  transition: theme.transitions.create(['opacity', 'transform'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  width: '500px',
  fontSize: '1.6rem',
  border: '1px solid black',
  padding: '10px',
  '&:hover': {
    opacity: 0.6,
    cursor: 'pointer',
  },
}))

const connectors = [
  {
    title: 'Metamask',
    connectorId: ConnectorNames.Injected,
  },
  {
    title: 'WalletConnect',
    connectorId: ConnectorNames.WalletConnect,
  },
]

const ModalLogin = () => {
  const { login } = useAuth()
  const dispatch = useAppDispatch()

  return (
    <>
     <LoginWallet
        onClick={() => {
          if (window.ethereum && window.ethereum.isMetaMask && window.ethereum.isMetaMask === true) {
            login(connectors[0].connectorId)
            localStorage.setItem(connectorLocalStorageKey, connectors[0].connectorId)
            dispatch(clearModal())
          } 
        }}
      >
        <Typography>Metamask</Typography>
      </LoginWallet>
      <LoginWallet
        onClick={() => {
          if (window.ethereum && window.ethereum.isMetaMask && window.ethereum.isMetaMask === true) {
            login(connectors[1].connectorId)
            localStorage.setItem(connectorLocalStorageKey, connectors[1].connectorId)
            dispatch(clearModal())
          } 
        }}
      >
        <Typography>Wallet Connect</Typography>
      </LoginWallet>
    </>

  )
}

export default ModalLogin
