import { ethers } from 'ethers'

export const deposit = async (cyballRewardContract, id, token, max, total, signer, account) =>
  cyballRewardContract.methods
    .deposit(id, token, max, total, signer)
    .send({
      from: account,
    })
    .on('transactionHash', (tx) => tx.transactionHash)

export const getRewardInfo = async (cyballRewardContract, id) => cyballRewardContract.methods.rewards(id).call()

export const approveContract = async (tokenContract, approvedContractAddress, account) =>
  tokenContract.methods.approve(approvedContractAddress, ethers.constants.MaxUint256).send({
    from: account,
  })
