import React from 'react'
import clsx from 'clsx'
import { withStyles } from '@mui/styles'
import Box from '@mui/material/Box'
import Tooltip from '@mui/material/Tooltip'
import Typography from '@mui/material/Typography'

const style = () => ({

  // border: {
  //   padding: '5px',
  //   textAlign: 'center',
  // },
  rejected: {
    // border: '1px solid red',
    color: '#ed3652',
    '&:hover': {
      cursor: 'pointer',
    },
  },
  approved: {
    // border: '1px solid #fcbe33',

    color: '#fcbe33',
  },
  inProcess: {
    // border: '1px solid #6a97ff',
    color: '#6a97ff',
  },
  readyClaim: {
    // border: '1px solid #fdcd26',
    color: '#fdcd26',
  },
  done: {
    // border: '1px solid #6eff78',
    color: '#6eff78'
  },
  confirming: {
    // border: '1px solid c3cdcc',
    color: '#c3cdcc'
  }
})

const StatusBar = ({ status, classes, rejectMessage }) => {
  const onGenerateStatus = (st) => {
    switch (st) {
      case 'approved':
        return 'Approved'
      case 'reject':
        return 'Rejected'
      case 'confirming':
        return 'Confirming'
      case 'done':
        return 'Done'
      case 'in-process':
        return 'In-Process'
      case 'ready-claim':
        return 'Ready-claim'
      default:
        return null
    }
  }

  if (status === 'reject') {
    return (
      <Tooltip title={<Typography variant="caption">{rejectMessage}</Typography>} placement="top" arrow>
        <Box className={clsx(
          classes.border,
          status === 'reject' && classes.rejected,
        )}>
          <Typography>{onGenerateStatus('reject')}</Typography>
        </Box>
      </Tooltip>
    )
  }

  return (
    <Tooltip title={<Typography variant="caption">{rejectMessage}</Typography>} placement="top" arrow>
      <Box
        className={clsx(
          classes.border,
          status === 'approved' && classes.approved,
          status === 'in-process' && classes.inProcess,
          status === 'ready-claim' && classes.readyClaim,
          status === 'done' && classes.done,
        )}
      >
        <Typography>{onGenerateStatus(status)}</Typography>
      </Box>
    </Tooltip>
  )
}

export default withStyles(style)(StatusBar)
