const ActionButton = ({ isLoading, isApproved, onApprove, dispatch, query, data, onRefetchingDepositInfo }) => {
  const { startTime, endTime } = query

  if (isLoading) {
    return <ButtonComponent isLoading />
  }

  if (isApproved) {
    return (
      <ButtonComponent
        onClick={() => {
          dispatch(
            setModal({
              modalName: MODAL.MODAL_DEPOSIT,
              data: {
                startTime,
                endTime,
                data,
                onRefetchingDepositInfo,
              },
            }),
          )
        }}
      >
        Deposit
      </ButtonComponent>
    )
  }

  return (
    <ButtonComponent onClick={onApprove} isLoading={isLoading}>
      Approve token
    </ButtonComponent>
  )
}

const Summary = ({ data, query, setQuery, refetchingData, rewardData, onUpdateRewardStatus, onEnterSignerPrivateKey }) => {
  const [expanded, setExpanded] = useState(true)
  const { account, chainId } = useWeb3React()
  const web3 = useWeb3()
  const [isLoading, setIsLoading] = useState(false)
  const [refetchingDepositInfo, setRefetchingDepositInfo] = useState(0)
  const dispatch = useAppDispatch()
  const depositInfo = useSelector(selectDepositInfo)
  const cyballRewardContract = useCyballRewardContract(configs.REWARD_CONTRACT_ADRESS)
  const [dataSummary, setDataSummary] = useState(null)
  const [isShowEnterPrivateKey, setShowEnterPrivateKey] = useState(false)
  // const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState(null)
  const [privateKey, setPrivateKey] = useState('')
  const [onPresentAlert] = useAlert()
  const { rewardStatus, startTime, endTime, rewardId } = query
  const n_rewardId = useMemo(() => generateRewardId(startTime, endTime), [startTime, endTime])


  useEffect(() => {
    const fetchRewardInfo = async () => {
      const res = await getRewardInfo(cyballRewardContract, rewardId !== '' ? rewardId : n_rewardId)

      const formatedResponse = { ...formatDepositInfo(res), rewardId: rewardId !== '' ? rewardId : n_rewardId }
      dispatch(setDepositInfo(formatedResponse,))
    }

    fetchRewardInfo()

  }, [rewardId, cyballRewardContract, refetchingDepositInfo])

  useEffect(() => {

    const fetchRewardInfo = async () => {
      const res = await getRewardInfo(cyballRewardContract, rewardId !== '' ? rewardId : n_rewardId)
      const formatedResponse = { ...formatDepositInfo(res), rewardId: rewardId !== '' ? rewardId : n_rewardId }
      dispatch(setDepositInfo(formatedResponse))
    }
    fetchRewardInfo()
  }, [rewardId])

  // Fetch reward summary
  useEffect(() => {
    const fetchSummary = async () => {

      // setIsLoading(true)
      try {
        const rewardSumRes = await getRewardSummary(startTime, endTime, '', rewardStatus)
        // console.log
        // const summaryData = get(rewardSumRes, 'data',{})
        // console.log(summaryData, 'summarydata')
        setDataSummary(rewardSumRes.data)
        // setIsLoading(false)
      } catch (err) {
        setError(err)
        // setIsLoading(false)
      }
    }

    if (account) {
      fetchSummary()
    }
  }, [account, query])

  const enterPrivateKey = () => {
    var pk = window.prompt("Enter Signer's private key","");
    try {
      if (web3) {
        const { address } = web3.eth.accounts.privateKeyToAccount(pk)
        if (address.toLowerCase() == depositInfo.signer.toLowerCase()) {
          setPrivateKey(pk)
          setShowEnterPrivateKey(true)
        }
        else {
          setShowEnterPrivateKey(false)
          alert("Wrong private key, Address of your private key is " + address)
        }
      }
    } catch (error) {
      setShowEnterPrivateKey(false)
      onPresentAlert(ALERT_TYPE.ERROR, error.message)
    }
  }


  const onHandleSignData = async () => {
    var confirmed = confirm("Please confirm this action!")
    if (!confirmed) return

    try {
      if (window.isSigningData) return
      window.isSigningData = true
      let newRewardData = [...rewardData]
      // chainId, contratRewardAddress, claimId, rewardId, account, amount, pKey
      for (let index = 0; index < rewardData.length; index += 1) {

        const status = get(rewardData, `${index}.status`, '')
        if(status !== REWARD_STATUS_CONSTANT.APPROVED){
          continue;
        }
        const id = get(rewardData, `${index}.id`, null)
        const amount = get(rewardData, `${index}.amount`, null)
        const address = get(rewardData, `${index}.address`, null)
        const decimalAmount = getDecimalAmount(amount, configs.CBT_TOKEN_DECIMALS)
        const { rewardId } = depositInfo
        if (id && amount) {
          const proof = await generateProofClaim(
            chainId,
            configs.REWARD_CONTRACT_ADRESS,
            rewardId,
            id,
            address,
            decimalAmount.toString(),
            privateKey,
          )
          await rewardSign(id, rewardId, JSON.stringify(proof))
    
          newRewardData = newRewardData.map((item) => {
            if (item.id === id) {
              return {
                ...item,
                status: REWARD_STATUS_CONSTANT.READYCLAIMED,
              }
            }

            return item
          })
          onUpdateRewardStatus(newRewardData)
        } else {

          throw new Error('Cant get id or amount')
        }
      }

      onPresentAlert(ALERT_TYPE.SUCCESS, 'Successfully sign data')
      window.isSigningData = false
    } catch (error) {
      console.error(error)
      window.isSigningData = false
      onPresentAlert(ALERT_TYPE.ERROR, 'Fail to sign data')
    }
  }

  return (
    <div>

      <Box
        sx={{
          padding: (theme) => theme.spacing(0, 1),
          display: 'flex',
          width: '100%',
          alignItems: 'flex-start',
          flexDirection: 'column'
        }}
      >
        <Box sx={{display: 'flex'}}>
            <RewardSesstions query={query} setQuery={setQuery} depositInfo={depositInfo} />
            <DepositAction data={dataSummary} query={query} />
        </Box>
        <Box sx={{ mt: '20px' }}>
          {/* {depositInfo.signer !== ZERO_ADDRESS && (
            <Typography >
              Reward Id: {rewardId !== '' ? rewardId : n_rewardId}
            </Typography>
          )} */}
          <Typography >
            Signer: {depositInfo.signer} <button onClick={() => enterPrivateKey()}>Enter Private Key</button>
          </Typography>
          {/* <Typography >
            Token: {depositInfo.token}
          </Typography> */}
          {/* <Typography>
            Max Amount: {get(depositInfo, 'maxAmount')}
          </Typography> */}
          {/* <Typography>
            Amount: {get(depositInfo, 'total')}
          </Typography> */}

        </Box>
        {(rewardStatus === REWARD_STATUS_CONSTANT.APPROVED && depositInfo.signer !== ZERO_ADDRESS && isShowEnterPrivateKey) && (
        // <SignData
        //   depositInfo={depositInfo}
        //   query={query}
        //   refetchingData={refetchingData}
        //   rewardData={rewardData}
        //   onUpdateRewardStatus={onUpdateRewardStatus}
        // />
          <ButtonComponent
            isLoading={isLoading}
            onClick={onHandleSignData}
          >
            Sign All Approved Request With: <b>{depositInfo.rewardId}</b>
          </ButtonComponent>
          )
        }
        {error && <Typography color='red'>{error.message}</Typography>}
      </Box>

    </div>
  )
}

export default Summary
