import { useCallback } from 'react'
import { useWeb3React } from '@web3-react/core'
import { deposit } from 'utils/callHelpers'
import configs from 'config'
import { useCyballRewardContract } from './useContract'


const useReward = () => {
  const { account } = useWeb3React()
  const cyballRewardContract = useCyballRewardContract(configs.REWARD_CONTRACT_ADRESS)
  const handleDeposit = useCallback(
    async (id, token, max, total, signer) => {
      const txHash = await deposit(cyballRewardContract, id, token, max, total, signer, account)
      return txHash
    },
    [account],
  )

  return { onDeposit: handleDeposit }
}

export default useReward

