import React, { useMemo } from 'react'
import { Route, Redirect, useLocation } from 'react-router-dom'
import { connectorLocalStorageKey, signedStorageKey } from './web3React'

const AuthRoute = ({ component: Component, ...rest }) => {
  const { pathname, search } = useLocation()
  const connectorId = window.localStorage.getItem(connectorLocalStorageKey)
  const signedData = window.sessionStorage.getItem(signedStorageKey)
  const isAuthenticated = useMemo(() => connectorId && signedData, [connectorId, signedData])
  if (!isAuthenticated) {
    return (
      <Redirect
        to={{
          pathname: '/login',
          state: {
            from: pathname + search,
          },
        }}
      />
    )
  }

  return <Route render={(props) => <Component {...props} />} {...rest} />
}

export default AuthRoute
