/* eslint-disable no-restricted-properties */
const abbreviate_number = function (num, fixed) {
    if (num === null) {
      return null
    } // terminate early
    if (num === 0) {
      return '0'
    } // terminate early
    fixed = !fixed || fixed < 0 ? 0 : fixed // number of decimal places to show
    const b = num.toPrecision(2).split('e') // get power
    const k = b.length === 1 ? 0 : Math.floor(Math.min(b[1].slice(1), 14) / 3) // floor at decimals, ceiling at trillions
    const c = k < 1 ? num.toFixed(0 + fixed) : (num / Math.pow(10, k * 3)).toFixed(1 + fixed) // divide by power
    const d = c < 0 ? c : Math.abs(c) // enforce -0 is 0
    const e = d + ['', 'K', 'M', 'B', 'T'][k] // append power
    return e
  }
  const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
  })
  
  export const formatCurrency = (value, fixed = 3) => {
    if (isNaN(value)) {
      return 0
    }
    if (value === null) {
      return null
    } // terminate early
    if (value === 0) {
      return '0'
    } // terminate early
    return new Intl.NumberFormat().format(parseFloat(value.toFixed(fixed)))
    // if (parseFloat(value) < 1000000) return new Intl.NumberFormat().format(parseFloat(value.toFixed(fixed)))
    // return abbreviate_number(value, fixed)
  }
  export const formatCurrencyVolumeSummary = (value, fixed = 3) => {
    if (isNaN(value)) {
      return formatter.format(0)
    }
    if (value === null) {
      return null
    } // terminate early
    if (value === 0) {
      return '0'
    } // terminate early
    return formatter.format(value.toFixed(fixed)) /* $2,500.00 */
  }
  