import React, { useMemo } from 'react'
import { useWeb3React } from '@web3-react/core'
import { Redirect, useLocation } from 'react-router-dom'
import Typography from '@mui/material/Typography'
import { connectorLocalStorageKey, signedStorageKey } from 'utils/web3React'
import ButtonComponent from 'components/Button'
import useAuth from 'hooks/useAuth'

const Login = () => {
  const { account } = useWeb3React()
  const { state = {} } = useLocation()
  const { signData } = useAuth()
  // console.log(useLocation(), 'state ?')
  const { from } = state
  const connectorId = window.localStorage.getItem(connectorLocalStorageKey)
  const signedData = window.sessionStorage.getItem(signedStorageKey)
  const returnedUrl = useMemo(() => {
    if (from) {
      return from
    }

    return '/reward-management'
  }, [from])

  const isLoggedIn = useMemo(() => {
    if (!account || !connectorId || !signedData) {
      return false
    }

    return true
  }, [account, connectorId, signedData])

  if (isLoggedIn) {
    return <Redirect to={returnedUrl} />
  }

  return (<>
    <Typography variant="h2">Please log in and sign</Typography>
    {/* <ButtonComponent onClick = {()=>{signData(account.toLocaleLowerCase())}} >sign to log in</ButtonComponent> */}
  </>)
}

export default Login
