import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import Typography from '@mui/material/Typography'
import Box from '@mui/material/Box'
import ButtonComponent from 'components/Button'
import useAlert from 'components/Alert/useAlert'
import LabelInput from 'components/LabelInput'
import { clearModal, selectModalData } from 'state/reducers/app'
import { rewardApprovement } from 'service/api'
import ALERT_TYPE from 'constant/alert'
import { useAppDispatch } from 'state'
import REWARD_STATUS_CONSTANT from 'constant/rewardStatus'


const ModalRejectReward = () => {
  const { id, rewardList, onUpdateRewardStatus } = useSelector(selectModalData)
  const [onPresentAlert] = useAlert()
  const [isLoading, setIsLoading] = useState(false)
  const dispatch = useAppDispatch()
  const [message, setMessage] = useState('')
  const onHandleApprove = async () => {
    try {
      setIsLoading(true)
      await rewardApprovement(id, false, message)
      onPresentAlert(ALERT_TYPE.SUCCESS, 'Successfully reject reward')
      setIsLoading(false)
      let newRewardData = [...rewardList]
    
      newRewardData = newRewardData.map((item) => {
        if (item.id === id) {
          return {
            ...item,
            message,
            status: REWARD_STATUS_CONSTANT.REJECTED,
          }
        }

        return item
      })
      onUpdateRewardStatus(newRewardData)
      dispatch(clearModal())
    
    
    } catch (error) {
      setIsLoading(false)
      onPresentAlert(ALERT_TYPE.ERROR, 'Fail to reject reward')
    }
  }

  return (
    <>
      <Typography mb={(theme) => theme.spacing(1.5)}>Id: {id}</Typography>
      <LabelInput sx = {{width: '100%', marginTop: '10px'}} label="Message" value={message} onChange={(e) => setMessage(e.target.value)} />
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          mt: 2,
        }}
      >
        <ButtonComponent
          color="error"
          isLoading={isLoading}
          disabled={!message}
          onClick={onHandleApprove}
          style={{
            marginRight: '15px',
          }}
        >
          Submit
        </ButtonComponent>
      </Box>
    </>
  )
}

export default ModalRejectReward
