import Web3 from 'web3'
import secp256k1 from 'secp256k1'
import getRpcUrl from 'utils/getRpcUrl'

const getWeb3BasedOnChainId = (chainId) => {
  const RPC_URL = getRpcUrl(chainId)
  const httpProvider = new Web3.providers.HttpProvider(RPC_URL, { timeout: 10000 })
  const web3 = new Web3(httpProvider)

  return web3
}

const getWeb3NoAccount = () => getWeb3BasedOnChainId()

const cleanPrivateKey = (key) => {
  let newKey = null

  if (key.indexOf('0x') === 0) {
    newKey = key.slice(2)
    return newKey
  }

  return key
}

const generateProofClaim = async (chainId, contratRewardAddress, claimId, rewardId, account, amount, pKey) => {
  // eslint-disable-next-line no-debugger
  const msg = Web3.utils.soliditySha3(chainId, contratRewardAddress, claimId, rewardId, account, amount)
  const signkey = cleanPrivateKey(pKey)
  const msg2 = Buffer.from(msg.slice(2), 'hex')
  const privateKey = Buffer.from(signkey, 'hex')
  const sig = secp256k1.ecdsaSign(msg2, privateKey)
  const ret = {
    v: sig.recid + 27,
    r: Array.from(sig.signature.slice(0, 32)),
    s: Array.from(sig.signature.slice(32, 64)),
  }

  return ret
}

export { getWeb3NoAccount, getWeb3BasedOnChainId, generateProofClaim }
