import React, { useEffect, useState, useMemo } from 'react'
import { useWeb3React } from '@web3-react/core'
import { styled } from '@mui/material/styles'
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown'
import get from 'lodash'
import Box from '@mui/system/Box'
import Tooltip from '@mui/material/Tooltip'
import Button from '@mui/material/Button'
import Typography from '@mui/material/Typography'
import { getRewardSessions } from 'service/api'
import { formatCurrency } from 'utils/formatCurrency'
import { getBalanceNumber } from 'utils/formatBalance'

import { setDepositsRewardQuantity } from 'state/reducers/app'
import { useAppDispatch } from 'state'
import Table from 'components/Table'
import useLockBodyScroll from 'hooks/useLockBodyScroll'

import Modal from '@mui/material/Modal'
import { formatTimeFromTimestamp, formatDateFromTimestamp } from 'utils/dateTime'

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  minWidth: '900px',
  maxHeight: '500px',
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  // p: 4,
  // overflow:'scroll'
}

const RewardSesstionsInfo = ({ query, setQuery, depositInfo }) => {
  const { account } = useWeb3React()
  const dispatch = useAppDispatch()
  // const { rewardStatus = 'all', startTime = 0, endTime = new Date().setHours(23, 59, 59, 0) } = query
  const [data, setData] = useState(null)
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState(null)
  // const [rewardSesstion, setRewardSesstion] = useState(null)
  const { rewardId } = query
  const [open, setOpen] = useState(false)
  const handleOpen = () => setOpen(true)
  const handleClose = () => setOpen(false)
  const [blockScroll, allowScroll] = useLockBodyScroll()

  const columns = useMemo(
    () => [
      {
        id: 'createdAt',
        title: 'Created At',
        align: 'right',
        numeric: true,
        disablePadding: false,
        renderComponent: (item) => (
          <Box>
            <Tooltip title={<Typography>{item.id}</Typography>}>
              <Box>
                <Typography
                  sx={{
                    fontSize: '10px',
                  }}
                >
                  {formatTimeFromTimestamp(item.id.slice(0, 12))}
                </Typography>
                <Typography
                  sx={{
                    fontSize: '14px',
                  }}
                >
                  {formatDateFromTimestamp(item.id.slice(0, 12))}
                </Typography>
              </Box>
            </Tooltip>
          </Box>
        ),
      },

      {
        id: 'id',
        title: 'Id & Signer',
        align: 'left',
        renderComponent: (item) => (
          <Box>
            <Typography>{`${item.id}`}</Typography>
            <Typography sx={{ marginTop: '2px' }}>{`${item.signer}`}</Typography>
          </Box>
        ),
      },
      {
        id: 'maxAmount',
        title: 'Deposit Amount',
        align: 'right',
        numeric: true,
        disablePadding: false,
        renderComponent: (item) => <Typography>{formatCurrency(getBalanceNumber(item.max))}</Typography>,
      },
      {
        id: 'amount',
        title: 'Current Amount',
        align: 'right',
        numeric: true,
        disablePadding: false,
        renderComponent: (item) => <Typography>{formatCurrency(getBalanceNumber(item.total))}</Typography>,
      },
    ],
    [data],
  )
  // get secction reward
  const fetchRewardSesstions = async () => {
    setIsLoading(true)
    try {
      const rewardSesstions = await getRewardSessions(0)
      // const summaryData = get(rewardSesstions, 'data', {})
      setData(rewardSesstions.data.rewards)
      dispatch(setDepositsRewardQuantity(rewardSesstions.data.total))
      setIsLoading(false)
    } catch (err) {
      setError(err)
      setIsLoading(false)
    }
  }
  useEffect(() => {
    if (account) {
      fetchRewardSesstions()
    }
  }, [account])
  useEffect(() => {
    if (open) {
      fetchRewardSesstions()
      blockScroll()
    } else {
      allowScroll()
    }
  }, [open])

  const handleListItemClick = (item) => {
    if (rewardId !== item.id) {
      setQuery &&
        setQuery({
          rewardId: item.id,
        })
    }
    setOpen(false)
  }

  return (
    <>
      {data && (
        <>
          <Button variant="outlined" onClick={handleOpen}>
            {depositInfo && depositInfo.rewardId && depositInfo.signer != '0x0000000000000000000000000000000000000000' ? (
              <>
                Reward:&nbsp;<b>{depositInfo.rewardId}</b>&nbsp;|&nbsp;
                <span style={{ color: 'green' }}>{depositInfo.total} CBT</span>
              </>
            ) : (
              'Select reward session'
            )}
            <ArrowDropDownIcon sx={{ ml: '5px' }} />
          </Button>
          <Modal
            open={open}
            onClose={handleClose}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
          >
            <Box sx={style}>
              <Table
                columns={columns}
                data={data}
                loading={isLoading}
                sx={{ maxHeight: 450 }}
                handleListItemClick={(item) => {
                  handleListItemClick(item)
                }}
              />
            </Box>
          </Modal>
        </>
      )}
      {error && <Typography color="red">{error.message}</Typography>}
    </>
  )
}

export default RewardSesstionsInfo
