import { useContext, useCallback } from 'react'
import { AlertContext } from 'context/AlertContext'

const useAlert = () => {
  const { onPresent } = useContext(AlertContext)

  const onPresentCallBack = useCallback((type, text) => {
    onPresent(type, text)
  }, [])

  return [onPresentCallBack]
}

export default useAlert
