import React, { useEffect, useState } from 'react'
import { useWeb3React } from '@web3-react/core'
import { styled } from '@mui/material/styles'
import get from 'lodash'
import Box from '@mui/system/Box'

import Typography from '@mui/material/Typography'
import REWARD_STATUS_CONSTANT from 'constant/rewardStatus'
import { getRewardSummary, getRewardSessions } from 'service/api'
import { formatCurrency } from 'utils/formatCurrency'
import DepositAction from '../DepositAction'

const Container = styled(Box)(({ theme }) => ({
    marginBottom: theme.spacing(3),
    display: 'flex',
    width: '100%',
}))

const ItemList = ({ title, value }) => {
    return (<Box sx={{ width: '100%', display: 'flex', justifyContent: "space-between" }}>
        <Typography color='text.secondary'>{`${title}:`}</Typography>
        <Typography>{formatCurrency(value)}</Typography>
    </Box>)

}

const RewardSummaryData = ({ query }) => {
    const { account } = useWeb3React()
    const { rewardStatus = 'all', startTime = 0, endTime = new Date().setHours(23, 59, 59, 0) } = query
    const [data, setData] = useState(null)
    // const [isLoading, setIsLoading] = useState(false)
    const [error, setError] = useState(null)




    // Fetch reward summary
    useEffect(() => {
        const fetchSummary = async () => {

            // setIsLoading(true)
            try {
                const rewardSumRes = await getRewardSummary(startTime, endTime, '', rewardStatus)
                // console.log
                // const summaryData = get(rewardSumRes, 'data',{})
                // console.log(summaryData, 'summarydata')
                setData(rewardSumRes.data)
                // setIsLoading(false)
            } catch (err) {
                setError(err)
                // setIsLoading(false)
            }
        }

        if (account) {
            fetchSummary()
        }
    }, [account, query])


    return (
        <Container>
            {data && <Box sx={{ width: '100%' , backgroundColor:'' }}>
                <ItemList title='Total Request' value={data.number} />
                <ItemList title='Max Reward' value={data.maxAmount} />
                <ItemList title='Total Reward' value={data.total} />
                {/* <DepositAction data = {data} query= {query}/> */}
            </Box>}
            {error && <Typography color='red'>{error.message}</Typography>}


        </Container>
    )
}

export default RewardSummaryData
