import { useEffect, useState } from 'react'
import useAuth from 'hooks/useAuth'
import { useWeb3React } from '@web3-react/core'
import { compareDate } from 'utils/dateTime'
import { connectorLocalStorageKey, signedStorageKey } from '../utils/web3React'
import useCurrentTime from './useTimer'

const useEagerConnect = () => {
  const { login, signData, logout } = useAuth()
  const tick = useCurrentTime()
  const [isShowSign, setIsShowSign] = useState(false)
  const { account } = useWeb3React()

  const checkToken = (data) => {
    if (data) {
      const { expireTime } = JSON.parse(data)
      const result = compareDate(new Date().getTime(), expireTime)
 
      return result
    }

    return false
  }

  useEffect(() => {
    const connectorId = window.localStorage.getItem(connectorLocalStorageKey)
    if (connectorId) {
      login(connectorId)
    }
  }, [login])

  useEffect(() => {
    const signedData = sessionStorage.getItem(signedStorageKey)
    const signDataFunc = async () => {
      try {
        setIsShowSign(true)
        await signData()
        setIsShowSign(false)
      } catch (error) {
        setIsShowSign(false)        
      }
    }
    // Sign data if user login via metamask but not sign data yet
    if (account && !signedData && !isShowSign) {
      signDataFunc()
    }
  }, [tick])

  useEffect(() => {
    const signedData = sessionStorage.getItem(signedStorageKey)
    const isTokenValid = checkToken(signedData)
  
    if (signedData && !isTokenValid && account) {
      logout()
      sessionStorage.removeItem(signedStorageKey)
    }
  }, [tick])
}
export default useEagerConnect
