import { useCallback } from 'react'
import { useWeb3React } from '@web3-react/core'
import useWeb3 from 'hooks/useWeb3'
import { approveContract } from 'utils/callHelpers'

export const useApprove = (tokenContract, approvedContractAddress) => {
  const { account } = useWeb3React()

  const handleApprove = useCallback(async () => {
    try {
      const tx = await approveContract(tokenContract, approvedContractAddress, account)
      return tx
    } catch (e) {
      console.log(e, 'error ?')
      return false
    }
  }, [account, tokenContract, approveContract])

  return { onApprove: handleApprove }
}
