import { useWeb3React } from '@web3-react/core'
import { useCallback } from 'react'
import { getAuction, getCurrentPrice } from 'utils/callHelpers'
import { useCyblocMarketContract } from './useContract'
import useWeb3 from './useWeb3'

const useSignMessage = () => {
  const { account } = useWeb3React()
  const web3 = useWeb3()

  const handleSignMessage = useCallback(
    async (message) => {
      if (account) {
        try {
          const signedData = await web3.eth.personal.sign(message, account)
          return { signedData }
        } catch (error) {
          console.log(error, 'sign error?')
          return { error }
        }
      }
      return null
    },
    [account, web3],
  )
  return { onHandelSigMessage: handleSignMessage }
}

export default useSignMessage
