import React, { useState } from 'react'
import Typography from '@mui/material/Typography'
import Box from '@mui/material/Box'
import ButtonComponent from 'components/Button'
import useAlert from 'components/Alert/useAlert'
import { clearModal, selectModalData, selectDepositsRewardQuantity } from 'state/reducers/app'
import ALERT_TYPE from 'constant/alert'
import { useAppDispatch } from 'state'
import useReward from 'hooks/useReward'
import { useSelector } from 'react-redux'
import { getDecimalAmount } from 'utils/formatBalance'
import { newGenerateRewardId } from 'utils/reward'
import LabelInput from 'components/LabelInput'
import configs from 'config'
import {formatCurrency} from 'utils/formatCurrency'

const ModalDeposit = () => {
  const [onPresentAlert] = useAlert()
  const [isLoading, setIsLoading] = useState(false)
  const [signer, setSigner] = useState('')

  const {  data } = useSelector(selectModalData)
  const { maxAmount, total, minAmount } = data
  const  quantityDepositRewards = useSelector(selectDepositsRewardQuantity)
  const [amount, setAmount] = useState(total)
  const { onDeposit } = useReward()
  const dispatch = useAppDispatch()


  const onHandleDeposit = async () => {
    try {
      if (!signer) {
        onPresentAlert(ALERT_TYPE.ERROR, 'Signer is required!')
        return
      }

      // if (amount<maxAmount) {
      //   onPresentAlert(ALERT_TYPE.ERROR, `Deposit amount must be more than max amount(${formatCurrency(maxAmount)}) !`)
      //   return
      // }
    
      if (quantityDepositRewards === null) {
        onPresentAlert(ALERT_TYPE.ERROR, 'Quantity Deposit is required! ')
        return
      }

      setIsLoading(true)
      const newId = newGenerateRewardId(new Date().getTime(),quantityDepositRewards )
      const decimalMaxAmount = getDecimalAmount(maxAmount, configs.CBT_TOKEN_DECIMALS)
      const decimalTotal = getDecimalAmount(amount, configs.CBT_TOKEN_DECIMALS)
 
      await onDeposit(
        newId,
        configs.CBT_TOKEN_REWARD,
        decimalTotal.toString(),// decimalMaxAmount,
        decimalTotal.toString(),
        signer,
      )
      setIsLoading(false)
      onPresentAlert(ALERT_TYPE.SUCCESS, 'Successfully deposit')
      dispatch(clearModal())
    } catch (error) {
      console.log(error, 'fail to rdeposit?')
      onPresentAlert(ALERT_TYPE.ERROR, `Fail to deposit! ${error.message}`)
      setIsLoading(false)
    }
  }

  return (
    <Box sx={{width: '500px'}}>
      <Typography mb={(theme) => theme.spacing(1.5)}>Are you sure you want to deposit?</Typography>
      <LabelInput
        label="Signer"
        value={signer}
        onChange={(e) => setSigner(e.target.value)}
        sxInput={{
          width: '100%',
          marginBottom: '5px'
        }}
      />
      
      <Box sx={{
          display: 'flex',
          justifyContent: 'center',
          mt: 2,
        }}>
  
      <LabelInput
        label="Deposit Amount"
        type='Number'
        value={amount}
        onChange={(e) => setAmount(e.target.value)}
        sxInput={{
          width: '100%',
          marginBottom: '5px'
        }}
      />
      </Box>
      {/* <Typography>
        Min reward amount: {formatCurrency(minAmount)}
      </Typography>
      <Typography>
        Max reward amount: {formatCurrency(maxAmount)}
      </Typography>
      <Typography>
        Total amount: {formatCurrency(total)}
      </Typography> */}

      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          mt: 2,
        }}
      >
        <ButtonComponent
          color="success"
         
          isLoading={isLoading}
          onClick={onHandleDeposit}
          style={{
            marginRight: '15px',
          }}
        >
          Confirm
        </ButtonComponent>
        <ButtonComponent
          color="error"
          disabled={isLoading}
          onClick={() => {
            dispatch(clearModal())
          }}
        >
          Cancel
        </ButtonComponent>
      </Box>
    </Box>
  )
}

export default ModalDeposit
