import React, { useState } from 'react'
import Web3 from 'web3'
import { styled } from '@mui/material/styles'
import Box from '@mui/system/Box'
import SearchIcon from '@mui/icons-material/Search'
import Select from 'components/Select'
import ButtonComponent from 'components/Button'
import LabelInput from 'components/LabelInput'

import Typography from '@mui/material/Typography'
import DatePicker from 'components/DatePicker'
import REWARD_STATUS_CONSTANT from 'constant/rewardStatus'
import RewardSummaryData from './RewardSummaryData'

const REWARD_STATUS = [
  {
    name: 'All',
    value: REWARD_STATUS_CONSTANT.ALL,
  },
  {
    name: 'In-Process',
    value: REWARD_STATUS_CONSTANT.INPROCESS,
  },
  {
    name: 'Rejected',
    value: REWARD_STATUS_CONSTANT.REJECTED,
  },
  {
    name: 'Approved',
    value: REWARD_STATUS_CONSTANT.APPROVED,
  },
  {
    name: 'Ready-Claim',
    value: REWARD_STATUS_CONSTANT.READYCLAIMED,
  },
  {
    name: 'Confirming',
    value: REWARD_STATUS_CONSTANT.CONFIRMING,
  },
  {
    name: 'Done',
    value: REWARD_STATUS_CONSTANT.DONE,
  },
]

const Container = styled(Box)(({ theme }) => ({
  marginBottom: theme.spacing(3),
  // display: 'flex',
}))
const isValidAddress = (adr) => {
  try {
    const web3 = new Web3()
    web3.utils.toChecksumAddress(adr)
    return true
  } catch (e) {
    return false
  }
}



const AdvancedFilters = ({ query, setQuery }) => {
  const { rewardStatus, startTime = null, endTime = null, address = null } = query
  const [filterAddress, setFilterAddress] = useState(address)

  return (
    <Container>
      <Box sx={{ display: ' flex', alignItems: 'flex-start'}}>
        <Box sx={{flex: 3}}>
          <Box
            sx={{
              display: 'flex',
              flexDirection: 'row',
            }}
          >
            <Select
              sxContainer={{
                mr: '15px',
                width: '150px',

              }}
              value={rewardStatus}
              onChange={(e) =>
                setQuery({
                  rewardStatus: e.target.value,
                })
              }
              options={REWARD_STATUS}
              label="Status"
            />
            <DatePicker
              sxContainer={{
                mr: '15px',
              }}
              hourConfig={{
                h: 0,
                m: 0,
                s: 0,
                ms: 0,
              }}
              value={startTime === 0 ? null : startTime}
              onChange={(newDate) => {
                if (newDate < endTime) {
                  setQuery({
                    startTime: newDate,
                  })
                } else {
                  alert('Start timer invalidate!')
                }
              }}

              label="Start Time"
            />
            <DatePicker
              hourConfig={{
                h: 23,
                m: 59,
                s: 59,
                ms: 0,
              }}
              value={endTime}
              onChange={(newDate) => {
                const now = new Date().getTime()
                const endDate = new Date(now - (now % 86400000) + 86400000)
                setQuery({
                  endTime: newDate,
                })
                // if (newDate <= endDate.getTime()) {
                // } else {
                //   alert('End Time invalidate ')
                // }

              }}

              label="End Time"
            />
          </Box>
          <Box sx={{ mt: '10px', display: 'flex' }}>
            {/* <LabelInput
              label="Filter Address"
              type="text"
              autoComplete='off'
              value={filterAddress}
              onChange={(e) => setFilterAddress(e.target.value)}

              sxInput={{
                mr: '10px',
                // marginTop: '10px',
                width: '100%',
                // maxWidth: '602px'
              }}
            /> */}
            <input type="text" autoComplete='off' style={{outline: 'none', width: '100%'}}
              placeholder="Filter Address"
              value={filterAddress} 
              onChange={(e) => setFilterAddress(e.target.value)} />
            <ButtonComponent
              sx={{ ml: '10px' }}
              onClick={() => {
                if (filterAddress && isValidAddress(filterAddress)) {
                  setQuery({
                    address: filterAddress,
                  })
                } else {
                  setQuery({
                    address: undefined,
                  })
                }
              }}
            // disabled={}
            >
              Appply
            </ButtonComponent>

          </Box>
        </Box>
        <Box
          sx={{
            flex: 2,
            paddingLeft: '20px',
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'space-between'
          }}
        >

          <RewardSummaryData query={query} />
          {/* <Button
          sx={{height: '40px'}}
          onClick={() => {
            setQuery({
              rewardStatus: 'all',
              startTime: 0,
              endTime: new Date().setHours(23, 59, 59, 0),
            })
          }}
        >
          Clear filters
        </Button> */}
        </Box>
      </Box>


    </Container>
  )
}

export default AdvancedFilters
