import cyballRewardAbi from 'config/abi/CybalReward.json'
import bep20Abi from 'config/abi/erc20.json'
import { getWeb3NoAccount } from './web3'
/**
 * Get contract object
 * @param {*} abi Abi of the contract
 * @param {*} address Address of the contract
 * @param {*} web3 Web3 instance
 * @returns Contract object
 */
const getContract = (abi, address, web3) => {
  // eslint-disable-next-line no-underscore-dangle
  const _web3 = web3 || getWeb3NoAccount()
  return new _web3.eth.Contract(abi, address)
}

export const getBep20Contract = (address, web3) => {
  if (address) {
    return getContract(bep20Abi, address, web3)
  }

  return null
}

export const getCyballRewardContract = (web3, address) => getContract(cyballRewardAbi, address, web3)
